.App {
  margin-top: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font-size: 1.2em;
}

.Form {
  display: grid;
  -ms-flex-pack: center;
      justify-content: center;
}


.Form input {
  text-align: center;
  padding: 10px 30px;
  font-size: 1.2em;
  border: 2px solid skyblue;
  font-family: 'Hind', sans-serif;
  font-weight: lighter;
}


.Task {
  padding: 10px 30px;
  margin: 15px 0;
  background-color: rgba(255, 255, 255, .7);
  border-radius: 5px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-transition: 250ms;
  -o-transition: 250ms;
  transition: 250ms;
  color: #444;
  border: 2px solid rgba(255, 255, 255, .5);
}

.Task:hover {
  cursor: pointer;
  background-color: rgba(234, 119, 119, 1);
  border: 2px solid rgb(255, 96, 96);
  color: #111;
}

.Task span {
}

.Task button {
  margin-left: 5px;
}



