body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-gradient(linear, left top, left bottom, from(#50ceb9), to(#50cec9));
  background: -webkit-linear-gradient(top, #50ceb9, #50cec9);
  background: -o-linear-gradient(top, #50ceb9, #50cec9);
  background: linear-gradient(to bottom, #50ceb9, #50cec9);
  height: 100vh;
  font-family: 'Hind', sans-serif;
}

#root {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}



